.footer_addverstment {
  border-radius: 18px;
  background: linear-gradient(
      90deg,
      rgba(0, 0, 0, 0.4) 0%,
      rgba(0, 0, 0, 0) 100%
    ),
    url(../../../assets/home/footer-img.png),
    lightgray 0px -242.25px / 103.364% 411.957% no-repeat;
  padding: 0px 36px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-position: center !important;
}

.footer_addverstment .footer_persentage {
  display: flex;
  align-items: center;
  gap: 16px;
}

.footer_persentage h3 {
  color: #fff;
  leading-trim: both;
  text-edge: cap;
  font-family: "Druk Wide Cy Web Bold Regular";
  font-size: 47.96px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
}

.footer_persentage h3 span {
  color: #d4ff27;
  leading-trim: both;
  text-edge: cap;
  font-family: "Druk Wide Cy Web Bold Regular";
  font-size: 47.96px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
}

.footer_persentage p {
  color: #fff;
  leading-trim: both;
  text-edge: cap;
  font-size: 16.194px;
  font-style: normal;
  font-weight: 700;
  line-height: 131%;
}

.footer_addverstment .footer_buttons {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 6px;
}

.footer_buttons .footer_shipping_btn {
  color: #fff;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%;
  padding: 12px 24px 14px 24px;
  border-radius: 100px;
  background: #1466d0;
}

.footer_buttons .footer_callus_btn {
  color: #000;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%;
  padding: 12px 24px 14px 24px;
  border-radius: 100px;
  background: #d4ff27;
}

/* media */

@media screen and (max-width: 820px) {
  .footer_persentage h3 {
    font-size: 25px;
  }
  .footer_persentage span {
    font-size: 25px;
  }
  .footer_shipping_btn {
    font-size: 12px !important;
  }
  .footer_callus_btn {
    font-size: 12px !important;
  }
}

@media screen and (max-width: 700px) {
  .footer_addverstment {
    display: flex;
    flex-direction: column;
  }
  .footer_buttons {
    display: flex !important;
    flex-direction: row !important;
    padding: 16px 36px;
  }
  .footer_persentage h3 {
    margin: 0;
  }
}

@media screen and (max-width: 480px) {
  .footer_buttons {
    width: 100%;
    justify-content: space-between !important;
    flex-direction: column !important;
    align-items: center !important;
    justify-content: center !important;
  }
  .footer_persentage {
    display: flex;
    flex-direction: column;
    gap: 0px !important;
    text-align: center;
  }
}
